import React, {useEffect, useState} from 'react';

const ProfileStatusWithHook = (props) => {

    let [editMode, setEditMode] = useState(false);
    let [statusHook, setStatusHook] = useState(props.status);

    useEffect ( () => {
        setStatusHook(props.status)
    }, [props.status] );

    const activateEditMode = () => {
        setEditMode(true);
    }

    const deactivateMode = () => {
        setEditMode(false);
        props.updateStatus(statusHook)
    }

    const changeStatus = (e) => {
        setStatusHook(e.currentTarget.value);
    }

    return (
        <div>
            {editMode ?
                <div>
                    STATUS: <input onBlur={deactivateMode} onChange={changeStatus} value={statusHook}></input>
                </div>
                :
                <div>
                    <span onDoubleClick={activateEditMode}>STATUS: {props.status || 'NOPE'}   </span>
                </div>
            }
        </div>
    );
}


export default ProfileStatusWithHook;