import React from 'react';
import cl from './MyPosts.module.css';
import Posts from './Post/Post.jsx';
import {Field, reduxForm} from "redux-form";
import {TextArea} from "../../FormsControls/FormControls";
import {maxLengthPostValidCreator, required} from "../../../Utilits/validators/validators";

const MyPosts = React.memo((props) => {

    let addPost = (values) => {
        props.addPost(values.myNewPostText);
    }

    return (
        <div>
            <div className={cl.newPosts}>
                <h3>My posts:</h3>
                <MyPostFormRedux onSubmit={addPost}/>
            </div>

            <div className={cl.post}>
                Previous posts:
                <Posts Posts={props.Posts.PostsData}/>
            </div>
        </div>
    )
});

const myPostMaxLength = maxLengthPostValidCreator(30);

const MyPostForm = React.memo((props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field component={TextArea} placeholder='Enter your text'
                       name='myNewPostText' validate={[required, myPostMaxLength]} />
            </div>
            <div>
                <button>New post</button>
            </div>
        </form>
    )
});


const MyPostFormRedux = reduxForm( {form:'myPostTextForm'}) (MyPostForm)

export default MyPosts;