import React from 'react';
import cl from './Post.module.css';

const PostEl = (props) => {
    return (
        <div className={cl.item}>
            <img src={props.imgSrc}/>
            {props.name} says "{props.message}"
            <div>
                <div className={cl.count}>{props.likeCount} Like</div>
            </div>
        </div>
    );
}

const Posts = (props) => {

    let Post = props.Posts
        .map( el => <PostEl imgSrc={el.imgSrc} name={el.name} likeCount={el.likeCount} message={el.message} />);

    return (
        <div className={cl.postsWrapper}>
            {Post}
        </div>
    );
}

export default Posts;