import React from 'react';
import cl from '../Profile.module.css';
import Preloader from "../../../assets/preloader/Preloader";
import yesImg from "../../../assets/images/yes.png";
import noImg from "../../../assets/images/no.png";
import ProfileStatusWithHook from "./ProfileStatusWithHooks";

const ProfileInfo = ({profile, status, updateStatus}) => {
    if (!profile) {
        return <Preloader/>
    }

    let jobPic
    if (profile.lookingForAJob === true) {
        jobPic = yesImg
    } else {
        jobPic = noImg
    }


    return (
        <div className={cl.profile}>
            <div>
                <img src={profile.photos.large}/>
                <span>
                    <h3>
                        <div>{profile.fullName}</div>
                        <div>{profile.aboutMe}</div>
                    </h3>
                    <h2>
                        <ProfileStatusWithHook status={status} updateStatus={updateStatus}/>
                    </h2>
                </span>
                <span>
                    <div>
                        facebook: {profile.contacts.facebook}
                    </div>
                    <div>
                        website: {profile.contacts.website}
                    </div>
                    <div>
                        vk: {profile.contacts.vk}
                    </div>
                    <div>
                        looking for a job: <img src={jobPic} className={cl.jobPic}/>
                        <div>{profile.lookingForAJobDescription}</div>
                    </div>

                </span>
            </div>
        </div>
    );
}

export default ProfileInfo;